import React from "react";
import { Helmet } from "react-helmet";
import Agreement from "components/common/Agreement";
import { Link } from "react-router-dom";
import { Modal, Checkbox, Toast } from "antd-mobile";
import ProgressPage from "./ProgressPage";
import Popup from "components/common/Popup";
import QueryString from "utils/query-string";
import success from 'assets/progress/success.png';
import fail from 'assets/progress/fail.png';
import iName from 'assets/progress/name.png';
import iIdCard from 'assets/progress/idcard.png';
import iPhone from 'assets/progress/phone.png';
import inputTips from 'assets/progress/input-tips.png';
import uploadPhotoImg from 'assets/progress/photo.png';

let query = {};
let mounted = false;
let nowStep = 0;

export default class extends React.PureComponent {
  state = {
    // step 1.
    key: "",
    originalPrice: 0,
    price: "0.00",
    discount: "限时五折",
    checked: true,
    showAgreement: false,
    // step 2.
    name: "",
    idNumber: "",
    phone: "",
    showConfirm: false,
    updatePhoto: false,
    photo: "",
    // step 3.
    responsed: false,
    status: false
  };
  handleInput = ({ target }) => {
    this.setState({
      [target.name]: target.value
    });
  };
  handleCheck = ({ target }) => {
    this.setState({
      checked: target.checked
    });
  };
  toPay = () => {
    if (this.state.checked) {
      const { history, location } = this.props;
      history.replace(
        `/progress/1?hP=1&s=1${
          !!location.search
            ? location.search.replace("?", "&")
            : ""
        }`
      );
    }
  };
  confirmInfo = () => {
    const { name, idNumber, phone } = this.state;
    const nameReg = /^[\u4e00-\u9fa5]+$/;
    const idNumberReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
    const phoneReg = /^[1][3,4,5,7,8][0-9]{9}$/;
    if (!name || !nameReg.test(name)) {
      Toast.info("请输入正确的姓名！", 1.6);
      return;
    }
    if (idNumber.length !== 18 || !idNumberReg.test(idNumber)) {
      Toast.info("请输入正确的身份证号码！", 1.6);
      return;
    }
    if (phone.length !== 11 || !phoneReg.test(phone)) {
      Toast.info("请输入正确的手机号码！", 1.6);
      return;
    }
    this.setState({ showConfirm: true });
  };
  submitInfo = () => {
    this.setState({
      updatePhoto: true
    });
  };
  uploadPhoto = ({ target }) => {
    if (!!target.files[0]) {
      Toast.loading("正在上传照片...", 0);
      const self = this;
      const photo = target.files[0];
      if (
        photo.type.split("/")[0] !== "image" ||
        photo.type.split("/")[1] === "gif"
      ) {
        Toast.info("图片格式错误！", 1);
        return;
      }
      const reader = new FileReader();
      reader.readAsDataURL(photo);
      reader.onload = function() {
        const { result } = this;
        new Promise(resolve => {
          if (photo.size / 1048576 <= 2) {
            resolve(result);
          } else {
            const img = new Image();
            img.src = result;
            img.onload = function() {
              const canvas = document.createElement("canvas");
              const ctx = canvas.getContext("2d");
              const { width, height } = this;
              let ratio = 1;
              if (width > 4096 || height > 4096) {
                ratio = width > height ? 4096 / width : 4096 / height;
              }
              canvas.width = width * ratio;
              canvas.height = height * ratio;
              ctx.drawImage(this, 0, 0, width * ratio, height * ratio);
              resolve(canvas.toDataURL(photo.type, 1048576 / photo.size));
            };
          }
        })
          .then(imgBase64 => {
            setTimeout(() => {
              self.setState({
                photo: imgBase64
              });
              Toast.info("上传成功！", 1);
            }, 500);
          });
      };
    }
  };
  comparePhoto = () => {
    if (!this.state.photo) {
      Toast.info("请先上传照片！", 1.6);
      return;
    }
    Toast.loading("正在验证身份信息...", 0);
    this.setState({ responsed: false });
    setTimeout(() => {
      this.setState({
        status: true
      });
      setTimeout(() => {
        Toast.hide();
        this.props.history.replace(`/progress/3${this.props.location.search}`);
      }, 200);
    }, 1000);
  };
  retry = () => {
    const { location, history } = this.props;
    history.push(
      `/progress/2${location.search}`
    );
  };
  toReport = () => {
    this.props.history.replace(`/report${this.props.location.search}`);
  };
  verify = () => {
    const { history, match } = this.props;
    const { step } = match.params;
    if (nowStep !== step) {
      nowStep = step;
      if (+step === 1) {
        const { hP } = query;
        if (!hP) {
          setTimeout(() => {
            Modal.alert(
              "提示",
              <>
                <p>你有未完成验证的报告, </p>
                <p>请点击查看我的报告继续验证</p>
              </>,
              [
                { text: "好的" },
                {
                  text: "去查看",
                  onPress: () =>
                    history.push(`/my/reports`)
                }
              ]
            );
          }, 800);
        }
      }
      if (+step === 3 && !this.state.responsed) {
        this.setState({ responsed: true });
      }
    }
  };
  componentDidMount() {
    nowStep = 0;
    mounted = false;
    this.verify();
    if (!!query.dK) {
      this.setState({
        key: query.dK
      });
    }
  }
  componentDidUpdate() {
    if (mounted) {
      this.verify();
    } else {
      mounted = true;
    }
  }
  render() {
    const {
      originalPrice,
      price,
      discount,
      checked,
      showAgreement,
      responsed,
      status,
      name,
      idNumber,
      phone,
      showConfirm,
      updatePhoto,
      photo
    } = this.state;
    const { match, history, location } = this.props;
    const { step } = match.params;
    query = QueryString.parse(location.search);
    const { hP, s, XP, dK } = query;
    return (
      <ProgressPage>
        <ProgressPage.Progress>
          <li className={+step > 0 ? "A" : ""}>
            提交订单
            <span />
          </li>
          <li className={+step > 1 ? "A" : ""}>
            输入信息
            <span />
          </li>
          <li className={+step > 2 ? "A" : ""}>生成报告</li>
        </ProgressPage.Progress>
        {+step === 1 ? (
          <>
            <Helmet>
              <title>提交订单</title>
            </Helmet>
            {!!hP ? (
              <ProgressPage.Status>
                <img
                  src={+s === 1 ? success : fail}
                  alt="status"
                  onClick={() => false}
                />
                <ProgressPage.Status.Text className={+s === 1 ? "" : "F"}>
                  支付{+s === 1 ? "成功" : "失败"}
                </ProgressPage.Status.Text>
                <ProgressPage.Button
                  onClick={() => {
                    if (+s === 1) {
                      history.replace(`/progress/2`);
                    } else {
                      history.replace(`/progress/1${location.search}`);
                    }
                  }}
                >
                  {+s === 1 ? "下一步" : "返回重新支付"}
                </ProgressPage.Button>
              </ProgressPage.Status>
            ) : (
              <ProgressPage.Payment>
                <ProgressPage.Payment.Title>
                  支付金额
                </ProgressPage.Payment.Title>
                <ProgressPage.Payment.Price
                  className={!!XP && +XP === 1 ? "grey" : ""}
                >
                  <p>
                    ￥<em>{price}</em>
                    {!!discount ? <span>{discount}</span> : ""}
                  </p>
                  <p className="S">原价：{originalPrice}元</p>
                </ProgressPage.Payment.Price>
                {!!XP && +XP === 1 ? (
                  <>
                    <div
                      style={{
                        marginTop: "0.3rem",
                        color: "#4EA0FF",
                        fontSize: "0.4267rem"
                      }}
                    >
                      体验Key
                    </div>
                    <ProgressPage.PersonalInfo.Input
                      style={{
                        marginTop: "0.2rem",
                        marginBottom: 0,
                        paddingLeft: 0
                      }}
                    >
                      <input
                        type="text"
                        name="key"
                        placeholder="请输入体验Key"
                        defaultValue={dK}
                        onInput={this.handleInput}
                      />
                    </ProgressPage.PersonalInfo.Input>
                  </>
                ) : (
                  ""
                )}
                <ProgressPage.Button
                  className={checked ? "" : "D"}
                  style={!!XP && +XP === 1 ? { marginTop: "0.5rem" } : {}}
                  onClick={this.toPay}
                >
                  立即{!!XP && +XP !== 1 ? "支付" : "体验"}
                </ProgressPage.Button>
                <ProgressPage.Payment.Agreement>
                  <Checkbox
                    onChange={this.handleCheck}
                    defaultChecked={checked}
                  />
                  我已阅读并同意
                  <span
                    onClick={() => {
                      this.setState({ showAgreement: true });
                    }}
                  >
                    《大数据查询授权协议》
                  </span>
                </ProgressPage.Payment.Agreement>
                <ProgressPage.Payment.Tip>
                  <p>支付后您可查看本人多纬度大数据信用报告，其中包括：</p>
                  <p>1.各项大数据信息，资质评分、手机号分析等信息；</p>
                  <p>2.各项风险信息，网贷、黑名单，逾期、失信等信息；</p>
                  <p>3.各项风险检测，高频、多头、羊毛党等风险检测。</p>
                </ProgressPage.Payment.Tip>
              </ProgressPage.Payment>
            )}
            <Agreement
              show={showAgreement}
              close={() => {
                this.setState({ showAgreement: false });
              }}
            />
          </>
        ) : (
          ""
        )}
        {+step === 2 ? (
          <ProgressPage.PersonalInfo>
            <Helmet>
              <title>输入信息</title>
            </Helmet>
            {!updatePhoto ? (
              <>
                <ProgressPage.PersonalInfo.Input>
                  <img
                    src={iName}
                    alt="name"
                    onClick={() => false}
                  />
                  <input
                    type="text"
                    name="name"
                    placeholder="请输入您的真实姓名"
                    onInput={this.handleInput}
                    defaultValue={name}
                  />
                </ProgressPage.PersonalInfo.Input>
                <ProgressPage.PersonalInfo.Input>
                  <img
                    src={iIdCard}
                    alt="idcard"
                    onClick={() => false}
                  />
                  <input
                    type="text"
                    name="idNumber"
                    maxLength="18"
                    placeholder="请输入您真实的身份证号"
                    onInput={this.handleInput}
                    defaultValue={idNumber}
                  />
                </ProgressPage.PersonalInfo.Input>
                <ProgressPage.PersonalInfo.Input>
                  <img
                    src={iPhone}
                    alt="phone"
                    onClick={() => false}
                  />
                  <input
                    type="tel"
                    name="phone"
                    maxLength="11"
                    placeholder="请输入您正确的手机号码"
                    onInput={this.handleInput}
                    defaultValue={phone}
                  />
                </ProgressPage.PersonalInfo.Input>
                <ProgressPage.Button
                  style={{ padding: "0.2667rem 0" }}
                  onClick={this.confirmInfo}
                >
                  下一步
                </ProgressPage.Button>
                <ProgressPage.PersonalInfo.Tips>
                  <p>注：请输入本人真实姓名以及正确的身份证号、手机号 </p>
                  <p>否则将无法查询到您正确的报告信息</p>
                </ProgressPage.PersonalInfo.Tips>
                <ProgressPage.PersonalInfo.Merits
                  src={inputTips}
                  alt="tips"
                  onClick={() => false}
                />
                <Popup style={{ display: showConfirm ? "block" : "none" }}>
                  <Popup.Dialog>
                    <Popup.CloseButton
                      onClick={() => {
                        this.setState({
                          showConfirm: false
                        });
                      }}
                    />
                    <Popup.Content>
                      <Popup.Content.Title>
                        <p>请再次确认</p>
                        <p>所输入信息是否一致</p>
                      </Popup.Content.Title>
                      <div>
                        <p style={{ marginBottom: "0.08rem" }}>
                          姓名：
                          <Popup.Content.Highlight>
                            {name}
                          </Popup.Content.Highlight>
                        </p>
                        <p style={{ marginBottom: "0.08rem" }}>
                          身份证号：
                          <Popup.Content.Highlight>
                            {idNumber}
                          </Popup.Content.Highlight>
                        </p>
                        <p>
                          手机号码：
                          <Popup.Content.Highlight>
                            {phone}
                          </Popup.Content.Highlight>
                        </p>
                      </div>
                      <Popup.Content.Tips>
                        <p>为保证您的信息安全，</p>
                        <p>即将使用人脸对比功能进行本人身份核验，</p>
                        <p>对比通过后，将为您生成您的大数据查询报告！</p>
                      </Popup.Content.Tips>
                    </Popup.Content>
                    <Popup.Buttons>
                      <button
                        onClick={() => {
                          this.setState({
                            showConfirm: false
                          });
                        }}
                      >
                        返回修改
                      </button>
                      <button
                        onClick={() => {
                          this.submitInfo();
                        }}
                      >
                        开始检测
                      </button>
                    </Popup.Buttons>
                  </Popup.Dialog>
                </Popup>
              </>
            ) : (
              <>
                <ProgressPage.PersonalInfo.Caption>
                  <p>为了您的信息安全，请拍摄/上传一张</p>
                  <p>与填写资料一致的人脸照片</p>
                  <p>用于验证是否为本人查询</p>
                </ProgressPage.PersonalInfo.Caption>
                <ProgressPage.PersonalInfo.Upload>
                  <span className="lT" />
                  <span className="rT" />
                  <span className="lB" />
                  <span className="rB" />
                  <img
                    src={
                      !!photo
                        ? photo
                        : uploadPhotoImg
                    }
                    alt="uploadPhoto"
                  />
                  {!photo ? <p>点击上传</p> : ""}
                  <input
                    type="file"
                    accept="image/*;capture=camera"
                    onChange={this.uploadPhoto}
                  />
                </ProgressPage.PersonalInfo.Upload>
                <ProgressPage.Button
                  className={!!photo ? "" : "D"}
                  style={{ marginTop: "1.3334rem" }}
                  onClick={() => {
                    this.comparePhoto();
                  }}
                >
                  提交验证
                </ProgressPage.Button>
                <ProgressPage.Status.Bottom>
                  <p
                    onClick={() => {
                      this.setState({
                        updatePhoto: false
                      });
                    }}
                  >
                    返回上一步修改资料
                  </p>
                </ProgressPage.Status.Bottom>
              </>
            )}
          </ProgressPage.PersonalInfo>
        ) : (
          ""
        )}
        {+step === 3 && responsed ? (
          <ProgressPage.Status>
            <Helmet>
              <title>人脸识别结果</title>
            </Helmet>
            <img
              src={status ? success : fail}
              alt="status"
              onClick={() => false}
            />
            <ProgressPage.Status.Text className={status ? "" : "F"}>
              {status ? "恭喜您，身份验证通过" : "对不起，身份验证失败"}
            </ProgressPage.Status.Text>
            <ProgressPage.Button
              onClick={() => (status ? this.toReport() : this.retry())}
            >
              {status ? "点击查看大数据信用报告" : "重新验证"}
            </ProgressPage.Button>
            {status ? (
              ""
            ) : (
              <>
                <ProgressPage.Status.Tips>
                  <p>
                    每天仅限<span>3次</span>验证机会
                  </p>
                </ProgressPage.Status.Tips>
                <ProgressPage.Status.Bottom>
                  <p>
                    信息不一致？
                    <Link to={`/progress/2${location.search}`}>
                      点此返回修改
                    </Link>
                  </p>
                  <p>
                    照片识别不通过，
                    <Link to={`/feedback${location.search}`}>点此反馈</Link>
                    <span>/</span>
                    <Link to="/">退出验证</Link>
                  </p>
                </ProgressPage.Status.Bottom>
              </>
            )}
          </ProgressPage.Status>
        ) : (
          ""
        )}
      </ProgressPage>
    );
  }
}
