import Styled from "styled-components";
import Button from "components/common/Button";

const ProgressPage = Styled.div`
    height: 100%;
    position: relative;
`;

ProgressPage.Progress = Styled.ul`
    width: 100%;
    top: 0;
    height: 0.96rem;
    color: #FFFFFF;
    font-size: 0.3734rem;
    text-align: center;
    line-height: 0.96rem;
    display: flex;
    justify-content: space-between;
    position: absolute;

    li {
        flex-basis: 3.3333rem;
        height: 100%;
        background-color: #D8D8D8;
        position: relative;

        &.A {
            background-color: #4EA0FF;
            &:after {
                border-left-color: #4EA0FF !important;
            }
        }

        span {
            width: 0.2134rem;
            height: 100%;
            top: 0;
            right: 0;
            background-color: #FFFFFF;
            display: block;
            position: absolute;
            z-index: 8;
        }

        &:nth-of-type(1):after, 
        &:nth-of-type(2):before, 
        &:nth-of-type(2):after, 
        &:nth-of-type(3):before {
            content: '';
            top: 0;
            border-top: 0.48rem solid transparent;
            border-bottom: 0.48rem solid transparent;
            border-left: 0.4267rem solid;
            display: block;
            position: absolute;
            z-index: 9;
        }

        &:nth-of-type(1):after,
        &:nth-of-type(2):after {
            right: -0.19rem;
            border-left-color: #D8D8D8;
            z-index: 10;
        }

        &:nth-of-type(2):before,
        &:nth-of-type(3):before {
            left: -0.02rem;
            border-left-color: #FFFFFF;
        }
    }
`;

ProgressPage.Content = Styled.div`
    height: 100%;
    padding-top: 0.96rem;
`;

ProgressPage.Payment = Styled(ProgressPage.Content)`
    padding-left: 1.3334rem;
    padding-right: 1.3334rem;
`;
ProgressPage.Payment.Title = Styled.div`
    margin-top: 3.2rem;
    color: #4A4A4A;
    font-size: 0.4267rem;
    text-align: center;
`;
ProgressPage.Payment.Price = Styled.div`
    margin-top: 0.5334rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    &.grey {
        filter: grayscale(100%);
    }

    p {
        color: #4EA0FF;
        font-size: 0.64rem;
        position: relative;

        em {
            font-size: 0.96rem;
            font-style: normal;
        }

        span {
            top: 0;
            left: 100%;
            padding: 0.0534rem 0.08rem;
            margin-left: 0.16rem;
            color: #FFFFFF;
            font-size: 0.32rem;
            white-space: nowrap;
            background-color: #E73B11;
            position: absolute;
        }
    }

    .S {
        margin-top: 0.16rem;
        color: #E73B11;
        font-size: 0.3734rem;
        text-decoration: line-through;
    }
`;
ProgressPage.Payment.Agreement = Styled.div`
    margin-top: 0.32rem;
    color: #C6C6C6;
    font-size: 0.32rem;
    display: flex;
    align-items: center;

    span {
        color: #4EA0FF;
    }
`;

ProgressPage.Payment.Tip = Styled.div`
    margin: 1.0667rem -0.4267rem 0;
    color: #C6C6C6;
    font-size: 0.32rem;
    line-height: 0.48rem;
`;

ProgressPage.PersonalInfo = Styled(ProgressPage.Content)`
    padding-top: 2.2934rem;
    padding-left: 1.0134rem;
    padding-right: 1.0134rem;
    display: flex;
    flex-direction: column;
`;
ProgressPage.PersonalInfo.Input = Styled.div`
    width: 100%;
    height: 1.12rem;
    padding-left: 0.5334rem;
    margin-bottom: 0.4rem;
    border: 1px solid #E2E3E6;
    border-radius: 3px;
    display: flex;
    align-items: center;

    &:last-of-type {
        margin-bottom: 0;
    }

    img {
        width: 0.7467rem;
        display: block;
    }

    input {
        flex-grow: 1;
        height: 100%;
        padding: 0 0.2667rem 0 0.32rem;
        color: #333333;
        font-size: 0.3734rem;
        border: none;

        &::-webkit-input-placeholder { 
            color: #C6C6C6; 
        } 
        &:-moz-placeholder { 
            color: #C6C6C6; 
        } 
        &::-moz-placeholder { 
            color: #C6C6C6; 
        } 
        &:-ms-input-placeholder { 
            color: #C6C6C6; 
        } 
    }
`;
ProgressPage.PersonalInfo.Tips = Styled.div`
    margin-top: 0.2134rem;
    color: #C6C6C6;
    font-size: 0.2934rem;
    line-height: 0.4267rem;
    text-align: center;
`;
ProgressPage.PersonalInfo.Merits = Styled.img`
    width: 100%;
    bottom: 0;
    left: 0;
    display: block;
    position: absolute;
`;

ProgressPage.PersonalInfo.Caption = Styled.div`
    margin: 0 -0.32rem;
    color: #4EA0FF;
    font-size: 0.3734rem;
    line-height: 0.5334rem;
    text-align: center;
`;

ProgressPage.PersonalInfo.Upload = Styled.div`
    width: 5.0667rem;
    height: 6.1334rem;
    margin: 1.0667rem auto 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;

    img {
        width: 3.7334rem;
        height: 4.5334rem;
        display: block;
    }

    span {
        width: 0.6667rem;
        height: 0.6667rem;
        position: absolute;
    }

    p {
        color: #4EA0FF;
        font-size: 0.3734rem;
    }

    input[type=file] {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0;
        position: absolute;
    }

    .lT {
        left: 0;
        top: 0;
        border-left: 2px solid #4EA0FF;
        border-top: 2px solid #4EA0FF;
    }

    .rT {
        right: 0;
        top: 0;
        border-right: 2px solid #4EA0FF;
        border-top: 2px solid #4EA0FF;
    }

    .lB {
        left: 0;
        bottom: 0;
        border-left: 2px solid #4EA0FF;
        border-bottom: 2px solid #4EA0FF;
    }

    .rB {
        right: 0;
        bottom: 0;
        border-right: 2px solid #4EA0FF;
        border-bottom: 2px solid #4EA0FF;
    }
`;

ProgressPage.Status = Styled(ProgressPage.Content)`
    padding-left: 1.3334rem;
    padding-right: 1.3334rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    img {
        width: 3.2rem;
        height: 3.2rem;
        margin-top: 4rem;
        display: block;
    }
`;
ProgressPage.Status.Text = Styled.p`
    width: 100%;
    margin-top: 0.5334rem;
    color: #00BE54;
    font-size: 0.4267rem;
    text-align: center;

    &.F {
        color: #E73B11;
    }
`;
ProgressPage.Status.Tips = Styled.div`
    margin-top: 0.5334rem;
    color: #4A4A4A;
    font-size: 0.32rem;
    line-height: 0.4267rem;
    text-align: center;

    span {
        color: #E73B11;
    }
`;
ProgressPage.Status.Bottom = Styled.div`
    width: 100%;
    margin-top: 0.64rem;
    color: #4EA0FF;
    font-size: 0.3734rem;
    line-height: 0.5334rem;
    text-align: center;

    p {
        margin-top: 0.4rem;
    }

    a {
        color: #4EA0FF;
        border-bottom: 1px solid;
    }

    span {
        margin: 0 0.08rem;
    }
`;

ProgressPage.Button = Styled(Button)`
    margin-top: 0.8rem;
`;

export default ProgressPage;
